.navigation {
	$root: &;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	visibility: hidden;
	overflow:0;
	width: 100%;
	height: 100%;
	padding: $default-padding $default-padding ($default-padding * 2);
	opacity: 0;
	background-color: $color-dark-grey;
	z-index: 1000;

	img {
		position: absolute;
		top: 15px;
		left: 15px;
	}
	 
	@include media-breakpoint-up(md) {
		opacity: 1;
		visibility: visible;
		top: 0;
		right: 0;
		bottom: auto;
		left: auto;
		width: auto;
		height: auto;
		position: absolute;
		background-color: transparent;

		img {
			display: none
		}
	}

	@include media-breakpoint-up(lg) {
		align-items: center;
	}

    &--animate {
    	animation-name: animateOut;
		animation-duration: 300ms;
		animation-fill-mode: forwards;
    }

	.navigation-opened & {
		animation-name: animateIn;
	}

	&__container {
		@include make-container( $grid-container-widths );
    	display: flex;
    	width: 100%;
    	max-width: 1600px;
    	flex-wrap: wrap;

    	@include media-breakpoint-up(lg) {
    		flex-wrap: nowrap;
    	}
	}

	&__navigation {
		display: flex;
		width: 100%;
		max-width: 360px;
		margin-top: ($default-margin * 5);
		margin-right: auto;
		margin-bottom: -$default-margin;
		margin-left: auto;
		flex-wrap: wrap;

		@include media-breakpoint-up(lg) {
			width: 60%;
			max-width: 100%;
		}

		@include media-breakpoint-up(xxl) {
			margin-top: ($default-margin * 10);
		}
	}

	&__menu {
		display: flex;
		flex-direction: column;
		width: 100%;
		text-align: center;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;

		@include media-breakpoint-up(md) {
			flex-direction: row;
			text-align: left;
			justify-content: flex-start;
			align-items: flex-start;
		}
	}

	&__item {
		.btn--white {
			margin: 0;

			@include media-breakpoint-up(md) {
				padding: 10px 15px 8px;
			}

		}
	}

	&__item + &__item {
		margin-top: $default-margin;
		margin-left: 0;

		@include media-breakpoint-up(md) {
			margin-top: 0;
			margin-left: 0;
		}
	}

	&__item:last-child {
		//margin-left: 10px;
	}

	&__item {
		display: inline-block;
		color: $color-blue-dark;
	}

	&__link {
			
	}

}
