@keyframes animateIn {
    0%, 1%, 100% {
        animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    }

    0% {
        visibility: hidden;

        opacity: 0;
    }

    1% {
        visibility: visible;

        opacity: 0;
    }

    100% {
        visibility: visible;

        opacity: 1;
    }
}
@keyframes animateOut {
    0%, 99%, 100% {
        animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    }

    0% {
        visibility: visible;

        opacity: 1;
    }

    99% {
        visibility: visible;

        opacity: 0;
    }

    100% {
        z-index: -1;

        visibility: hidden;

        opacity: 0;
    }
}