// =============================================================================
// Font Face
// =============================================================================
@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {
    $src: null;
    $extmods: ( eot: "?", svg: "#" + str-replace($name, " ", "_"));
    $formats: ( otf: "opentype", ttf: "truetype");
    @each $ext in $exts {
        $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
        $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
        $src: append($src, url(quote($path + "." + $extmod)) format(quote($format)), comma);
    }
    @font-face {
        font-family: quote($name);
        font-weight: $weight;
        font-style: $style;
        src: $src;
    }
}

@function calculate-container-padding($size, $container: $container-max-widths, $gutter: $grid-container-widths){
    @return map-get($container, $size) - map-get($gutter, $size);
}

//Arrows
@mixin arrow-base() {
    width: 0;
    height: 0;
}

@mixin arrow-up($size, $color: black) {
    @include arrow-base();
    border-left: $size solid transparent;
    border-right: $size solid transparent;
    border-bottom: $size solid $color;
}

@mixin arrow-down($size, $color: black) {
    @include arrow-base();
    border-left: $size solid transparent;
    border-right: $size solid transparent;
    border-top: $size solid $color;
}

@mixin arrow-right($size, $color: black) {
    @include arrow-base();
    border-top: $size solid transparent;
    border-bottom: $size solid transparent;
    border-left: $size solid $color;
}

@mixin arrow-left($size, $color: black) {
    @include arrow-base();
    border-top: $size solid transparent;
    border-bottom: $size solid transparent;
    border-right: $size solid $color;
}

@mixin sequential-transition-delay( $delay: 200ms, $count: 10, $base-delay: 0ms ) {
    @for $i from 0 to $count {
        &:nth-of-type(#{$i}) { transition-delay: $i * $delay + $base-delay; }
    }
}

/* Other */

@mixin container($max-width) {
    max-width: $max-width;
    margin: 0 auto;
    padding: 15px;
}