//  -----------------------------
//  Shades of grey
//  -----------------------------

$color-white: #ffffff;
$color-very-light-grey: #ececec;
$color-light-grey: #FFFDFB;
$color-medium-grey: #4a4a49;
$color-grey: #5f5f5f;
$color-medium-dark-grey: #ECECEC;
$color-dark-grey: #272624; //ok
$color-black: #000000;

//  -----------------------------
//  Brand colors
//  -----------------------------

$color-blue: #0770d7;
$color-blue-dark: #002250;
$color-yellow: #f9c300;
$color-gray: #f5f5f5;

//  -----------------------------
//  Margin variables
//  -----------------------------

$mini-margin: 10px;
$default-margin: 20px;
$large-margin: 30px;
$very-large-margin: 50px;

//  -----------------------------
//  Padding variables
//  -----------------------------

$mini-padding: 10px;
$default-padding: 20px;
$large-padding: 30px;
$very-large-padding: 50px;

//  -----------------------------
//  Grid variables
//  -----------------------------

$grid-gutter-width-base: $default-margin;

$grid-gutter-widths: (
    xs: $grid-gutter-width-base,
    sm: $grid-gutter-width-base,
    md: $grid-gutter-width-base,
    lg: $grid-gutter-width-base,
    xl: $grid-gutter-width-base,
    xxl: ($default-padding * 2),
    xxxl: $very-large-padding
);

$grid-container-widths: (
    xs: ( $grid-gutter-width-base * 2 ),
    sm: ( $grid-gutter-width-base * 2 ),
    md: ( $grid-gutter-width-base * 3 ),
    lg: ( $grid-gutter-width-base * 6 ),
    xl: ( $grid-gutter-width-base * 8 ),
    xxl: ( $grid-gutter-width-base * 10 ),
    xxxl: ( $grid-gutter-width-base * 12 ),
    xxxxl: ( $grid-gutter-width-base * 14 )
);

$grid-breakpoints: (
    xs: 0,
    sm: 520px,
    md: 768px,
    lg: 992px,
    xl: 1200px,
    xxl: 1400px,
    xxxl: 1600px,
    xxxxl: 1920px,
);

$container-max-widths: (
    xl: 1200px,
    xxl: 1400px,
    xxxl: 1600px
);
