html,
body {
	font-family: Avenir, sans-serif;
	font-size: 16px;
	font-weight: 300;
	font-style: normal;
	line-height: 25px;
	color: $color-blue-dark;
	background-color: #151514;

	-webkit-font-smoothing: antialiased;
}

::selection {
	color: $color-white;
	background: $color-grey;

	&:after {
		background-color: $color-white!important;
	}
}
