$hamburger-padding-x                       : $mini-padding !default;
$hamburger-padding-y                       : $mini-padding !default;
$hamburger-layer-width                     : 20px !default;
$hamburger-layer-height                    : 3px !default;
$hamburger-layer-spacing                   : 5px !default;
$hamburger-layer-color                     : $color-dark-grey !default;
$hamburger-layer-border-radius             : 0px !default;
$hamburger-hover-opacity                   : 0.7 !default;
$hamburger-hover-transition-duration       : 0.15s !default;
$hamburger-hover-transition-timing-function: linear !default;

$hamburger-hover-use-filter: false !default;
$hamburger-hover-filter    : opacity(50%) !default;

.hamburger {
	padding: 0;
	cursor: pointer;
	position: fixed;
	top: 0;
	right: 0;
	z-index: 1999;
	transition-property: opacity, filter;
	transition-duration: $hamburger-hover-transition-duration;
	transition-timing-function: $hamburger-hover-transition-timing-function;
	display: block;

	@include media-breakpoint-up(md) {
		display: none;
	}

	// Normalize (<button>)
	font: inherit;
	color: inherit;
	text-transform: none;
	background-color: transparent;
	border: 0;
	margin: 0 0 0 auto;
	overflow: visible;
	top: 30px;
	right: 20px;
	width: 36px;
	height: 36px;
}

.hamburger--squeeze {
	.hamburger-inner {
		top: $hamburger-layer-height / 2;

		&::before {
			top: $hamburger-layer-height + $hamburger-layer-spacing;
			transition-property: transform, opacity, background-color;
			transition-timing-function: ease;
			transition-duration: 0.15s;
		}

		&::after {
			top: ($hamburger-layer-height * 2) + ($hamburger-layer-spacing * 2);
		}
	}

	.navigation-opened & {
		.hamburger-inner {
			$y-offset: $hamburger-layer-spacing + $hamburger-layer-height;
			transform: translate3d(0, $y-offset, 0) rotate(45deg);

			&::before {
				transform: rotate(-45deg) translate3d($hamburger-layer-width / -7, $hamburger-layer-spacing * -1, 0);
				opacity: 0!important;
			}

			&::after {
				transform: translate3d(0, $y-offset * -2, 0) rotate(-90deg);
			}
		}
	}
}

.hamburger-box {
	width: $hamburger-layer-width;
	height: $hamburger-layer-height * 3 + $hamburger-layer-spacing * 2;
	display: block;
	position: relative;
}

.hamburger-inner {
	display: block;
	margin-top: $hamburger-layer-height / -2;

	&,
	&::before,
	&::after {
		width: $hamburger-layer-width;
		height: $hamburger-layer-height;
		background-color: $hamburger-layer-color;
		border-radius: $hamburger-layer-border-radius;
		position: absolute;
		transition-property: transform, background-color;
		transition-duration: 0.15s;
		transition-timing-function: ease;
		will-change: transform, background-color;

		.hamburger--inverted & {
			background-color: $color-white;
		}
	}

	&::before {
		width: $hamburger-layer-width;
	}

	&::before,
	&::after {
		content: "";
		display: block;
	}

	&::before {
		top: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
	}

	&::after {
		bottom: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
	}

	.navigation-opened & {
		background-color: $color-white;

		&::before,
		&::after {
			background-color: $color-white;
		}
	}
}
