@include font-face('Avenir', '../../assets/fonts/Avenir-Book', 300, normal, woff);
@include font-face('Avenir', '../../assets/fonts/Avenir-BookOblique', 300, italic, woff);
@include font-face('Avenir', '../../assets/fonts/Avenir-Heavy', 400, normal, woff);
@include font-face('Avenir', '../../assets/fonts/Avenir-HeavyOblique', 800, italic, woff);

h1 {
    font-size: 35px;
    font-weight: 800;
    line-height: 40px;
    color: $color-blue;

    @include media-breakpoint-up(md) {
        font-size: 45px;
        line-height: 50px;
    }
}

h2 {
    font-size: 40px;
    font-weight: 800;
    line-height: 46px;
}

h3 {
    font-size: 30px;
    font-weight: 800;
    line-height: 46px;
}