.modal {
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	width: 100%;
	height: 100%;
	transition-duration: 500ms;
	transition-property: opacity, z-index;
	pointer-events: none;
	opacity: 0;
	align-items: center;
	justify-content: center;
	will-change: all;
	z-index: -1;

	* {
		-webkit-tap-highlight-color: rgba(0,0,0,0)!important;
	}

	@include media-breakpoint-up(lg) {
		align-items: center;
	}

	.modal-opened & {
		pointer-events: all;
		opacity: 1;
		z-index: 999;
	}

	.admin-bar & {
		top: 32px;

		@media screen and (max-width:782px) {
			top: 46px;
		}
	}

	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($color-grey, 0.85);
	}

	&__content {
		position: relative;
		z-index: 2;
		top: 0;
		max-height: 100vh;
		transform: translate3d(0, -$large-margin, 0);
		transition-property: transform;
		transition-duration: 500ms;
		overflow: auto;
		-webkit-overflow-scrolling: touch;

		.modal-opened & {
			transform: translate3d(0, 0, 0);
		}

		.admin-bar & {
			top: -16px;
			max-height: calc( 100vh - 32px );

			@media screen and ( max-width: 782px ) {
				max-height: calc( 100vh - 46px );
				top: -23px;
			}
		}
	}

	&__close {
		position: absolute;
		top: $default-margin;
		right: $default-margin;
		cursor: pointer;
		width: 42px;
		height: 25px;

		svg {
			fill: $color-grey;

			path {
				&:first-child {
					transform: translate3d(0, -10px, 0);
				}

				&:nth-child(3) {
					transform: translate3d(0, 10px, 0);
				}
			}
		}
	}

	&__inner {
		width: 100%;
		max-width: 660px;
		background-color: $color-white;
		padding: $default-padding;
		position: relative;

		@include media-breakpoint-up(md) {
			padding: $large-padding;
		}

		@include media-breakpoint-up(lg) {
			padding: $very-large-padding + $default-padding $very-large-padding;
		}
	}
}