.header {
    @include container(100%);
    width: 100%;

}

.hero {
    text-align: center;
    margin-bottom: 72px;
    padding-top: 50px;

    @include media-breakpoint-up(md) {
        padding-top: 100px;
     }
 

    h1 {
        max-width: 800px;
        margin: 0 auto 20px auto;
    }

    span {
        display: block;
        max-width: 640px;
        margin: 0 auto 30px auto;
    }

    .btn {
        margin-bottom: 10px;
    }
}

.cta,
.need,
.how {

    padding: 72px 0;

    background: $color-blue-dark;
    color: $color-white;
    text-align: center;

    h2 {
        margin-bottom: 40px;
    }

    &__container {
        @include container(1280px);
    }

    &__row {
        @include make-row();
    }

    &__col {
        @include make-col-ready();
        @include make-col(12);
        margin-bottom: 50px;

        @include media-breakpoint-up(md) {
            @include make-col(6);
        }

        &--bordered {
            @include media-breakpoint-up(md) {
               border-right: 1px solid rgba(255,255,255,0.5);
            }
        }
  
        img {
            margin-bottom: 15px;
        }

        &:first-child {
            img {
                margin-bottom: 31px;
            }
        }

        h3 {
            color: $color-yellow;
        }

        strong {
            color: $color-yellow;
            font-weight: 800;
        }

        span {
            display: block;
            max-width: 400px;
            margin: 0 auto 20px auto;
        }

    }
}

.how {

    padding-top: 0;
    margin-bottom: -200px;

    &__container {
        max-width: 950px;
        margin: 0 auto;
    }

    &__col {
        
        text-align: center;
        margin-bottom: 10px;

        @include media-breakpoint-up(md) {
            margin-bottom: 0px;
            text-align: right;

            &:first-child {
                text-align: left;
            }
        }
    }

    h3 {
        color: $color-white;
    }

    &__video {
        position: relative;

        img {
            max-width: 100%;
            height: auto;
            display: block;
        }
    }

    &__tabs {
        padding-top: 20px;
    }

    &__tabs-link {
        display: inline-block;
        margin-left: 10px;
        font-weight: 800;
        cursor: pointer;

        &.active {
            color: $color-yellow;
            border-bottom: 3px solid $color-yellow;
        }
    }

    &__contents {
        margin-top: 30px;
        position: relative;
        background-color: #151514;

        > div {
            position: absolute;
            z-index: 1;
            top: 0;
            opacity: 0;
            transition-property: opacity;
            transition-duration: 500ms;

            &.active {
                position: relative;
                z-index: 2;
                opacity: 1;
            }
        }
    }
}

.youtube_embed {
    position: relative;
    padding-bottom: 56.25%;
 
    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.need{
    background: $color-gray;
    color: $color-blue-dark;
    text-align: center;
    padding-top: 150px;

    &__col {
        &--bordered {
            @include media-breakpoint-up(md) {
                border-right: 1px solid #cccccc;
             }           
        }
        @include media-breakpoint-up(md) {
            span {
                min-height: 75px;
            }
        }
    }

    h2 {
        color: $color-blue-dark;
    }

    h3 {
        color: $color-blue;
    }

    img {
        margin-bottom: 20px;
    }
}

.footer {
    @include container(100%);
    width: 100%;
    background: $color-blue-dark;
    font-size: 14px;
    color: rgba(255,255,255,0.5);

    &__row {
        @include make-row();
        margin-bottom: 10px;

        &:first-child {
            margin-bottom: 10px;
        }
    }

    &__col {
        @include make-col-ready();
        @include make-col(12);

        text-align: center;
        margin-bottom: 30px;

        @include media-breakpoint-up(md) {
            @include make-col(6);
            margin-bottom: 0px;

            text-align: left;

            &:last-child {
                text-align: right;
            }
        }
    }

    &__footer-links {
        color: $color-yellow;
    }

    &__footer-link {
        display: inline-block;
        margin-right: 10px;
        font-size: 20px;
        line-height: 46px;
        font-weight: 800;
        text-decoration: underline;
    }

    &__legal-link {
        margin-left: 20px;
        text-decoration: underline;
        white-space: nowrap;
    }

    .btn {
        font-size: 20px;
        font-weight: 800; 
        padding: 10px 30px 8px;
    }
}